<template>
  <div class="tool-bar">
    <button
      class="iconfont icon-bold"
      @click="execCommand('bold')"
      :class="[isBold ? 'display-bold' : '']"
    ></button>
    <button
      class="iconfont icon-list-unordered"
      @click="execCommand('insertUnorderedList')"
    ></button>
    <button
      class="iconfont icon-list-ordered"
      @click="execCommand('insertOrderedList')"
    ></button>
    <button class="iconfont icon-heading" @click="show('heading')"></button>
    <button
      class="iconfont icon-font"
      :class="[
        fontColor === 'blue' ? 'display-blue' : '',
        fontColor === 'yellow' ? 'display-yellow' : '',
        fontColor === 'red' ? 'display-red' : '',
      ]"
      @click="show('fontcolor')"
    ></button>
    <button class="iconfont icon-text" @click="show('fontsize')"></button>
    <button class="iconfont icon-link" @click="createLink"></button>
    <button class="nav__img iconfont icon-image">
      <input
        type="file"
        accept="image/gif, image/jpeg, image/png"
        @change="insertImg"
      />
    </button>
    <button
      class="iconfont icon-strikethrough-2"
      @click="execCommand('strikeThrough')"
    ></button>
    <button
      class="iconfont icon-underline"
      @click="execCommand('underline')"
    ></button>
    <button
      class="iconfont icon-undo-circle"
      @click="execCommand('undo')"
    ></button>
    <button
      class="iconfont icon-redo-circle"
      @click="execCommand('redo')"
    ></button>
    <div
      class="selected-options selected-heading"
      :class="[showDialogType === 'heading' ? '' : 'display-none']"
    >
      <button @click="execCommand('formatBlock', '<h1>')">H1</button>
      <button @click="execCommand('formatBlock', '<h2>')">H2</button>
      <button @click="execCommand('formatBlock', '<h3>')">H3</button>
      <button @click="execCommand('formatBlock', '<h4>')">H4</button>
      <button @click="execCommand('formatBlock', '<h5>')">H5</button>
      <button @click="execCommand('formatBlock', '<h6>')">H6</button>
      <button @click="execCommand('formatBlock', '<p>')">正文</button>
    </div>
    <div
      class="selected-options selected-fontcolor"
      :class="[showDialogType === 'fontcolor' ? '' : 'display-none']"
    >
      <button @click="execCommand('foreColor', 'black')">黑</button>
      <button @click="execCommand('foreColor', 'red')">红</button>
      <button @click="execCommand('foreColor', 'yellow')">黄</button>
      <button @click="execCommand('foreColor', 'blue')">蓝</button>
    </div>
    <div
      class="selected-options selected-fontsize"
      :class="[showDialogType === 'fontsize' ? '' : 'display-none']"
    >
      <button @click="changeSelectFontSize(10)">10px</button>
      <button @click="changeSelectFontSize(13)">13px</button>
      <button @click="changeSelectFontSize(16)">16px</button>
      <button @click="changeSelectFontSize(18)">18px</button>
      <button @click="changeSelectFontSize(24)">24px</button>
      <button @click="changeSelectFontSize(32)">32px</button>
      <button @click="changeSelectFontSize(48)">48px</button>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from 'vue';
export default {
  props: {
    showDialogType: {
      type: String,
      default: '',
      required: true,
    },
    isBold: {
      type: Boolean,
      default: false,
      required: true,
    },
    fontColor: {
      type: String,
      default: 'black',
      required: true,
    },
  },
  setup(props, ctx) {
    const state = reactive({
      boldFlag: false,
    });
    function execCommand(name, args = null) {
      if (name === 'bold') {
        ctx.emit('changeBold');
      } else if(name === 'formatBlock' || name === 'foreColor') {
        ctx.emit('hidden');
      }
      document.execCommand(name, false, args);
    }
    // 插入链接
    function createLink() {
      let url = window.prompt('请输入链接地址');
      if (url) execCommand('createLink', url);
    }
    // 插入图片
    function insertImg(e) {
      let reader = new FileReader();
      let file = e.target.files[0];
      reader.onload = () => {
        let base64Img = reader.result;
        execCommand('insertImage', base64Img);
        document.querySelector('.nav__img input').value = ''; // 解决同一张图片上传无效的问题
      };
      reader.readAsDataURL(file);
    }
    // 改变字体大小
    function changeSelectFontSize(size) {
      execCommand('styleWithCss', true);
      execCommand('fontSize', 5);
      const selectedRange = document.querySelector('.editor');
      const spans = selectedRange.querySelectorAll('span');
      console.log(state.fontSize);
      spans.forEach((span) => {
        let fontSize = span.style.fontSize;
        console.log(fontSize);
        if ('x-large' === fontSize) span.style.fontSize = size + 'px';
      });
      ctx.emit('hidden')
    }
    function show(type) {
      ctx.emit('show', type);
    }
    return {
      show,
      createLink,
      execCommand,
      insertImg,
      changeSelectFontSize,
      ...toRefs(state),
    };
  },
};
</script>

<style lang="scss" scoped>
@import './ToolBar.scss';
</style>
