<template>
  <div class="c-rich-editor">
    <div @mouseleave="hidden">
      <tool-bar
        :showDialogType="showDialogType"
        :isBold="isBold"
        :fontColor="fontColor"
        @show="show"
        @changeBold="changeBold"
        @hidden="hidden"
      />
      <div class="row">
        <div
          class="editor"
          contenteditable="true"
          @input="print"
          @click="getSelDetail"
          @keypress="getSelDetail"
          ref="editorRef"
        ></div>
        <div class="content" @click="hidden">{{ html }}</div>
      </div>
    </div>
    <button
      class="storage-btn"
      :class="[isOffLine ? 'disabled-btn' : 'enable-btn']"
      @click="editContent"
      :disabled="isOffLine"
    >
      存储
    </button>
    <button
      class="storage-btn"
      :disabled="!isOffLine"
      :class="[isOffLine ? 'enable-btn' : 'disabled-btn']"
      @click="saveOffContent"
    >
      离线存储
    </button>
    <button class="storage-btn enable-btn" @click="clearContent">清空</button>
  </div>
</template>

<script>
import ToolBar from './components/ToolBar.vue';
import { getContentByIdReq, editContentReq } from '../../api/content';
import { reactive, toRefs, ref, onMounted } from 'vue';
import { EventUtil } from '../../utils/utils';

export default {
  components: {
    ToolBar,
  },
  setup() {
    const editorRef = new ref(null);
    // 响应数据
    const state = reactive({
      html: '',
      showDialogType: '',
      isBold: false,
      fontColor: 'black',
      isSaveOffLine: false,
      isOffLine: false,
    });
    function print() {
      state.html = document.querySelector('.editor').innerHTML;
    }
    function hidden() {
      state.showDialogType = '';
    }
    function show(type) {
      state.showDialogType = type;
    }
    // 调用接口，获取指定 id 文本内容
    async function getContent() {
      const { data } = await getContentByIdReq('1645414530651');
      if (data.code == 200) {
        state.html = data.data.text; // 给展示html结构赋值
        editorRef.value.innerHTML = data.data.text; // 给可编辑区域赋值
        localStorage.setItem(
          'rich-text-storage',
          JSON.stringify(data.data.text)
        );
        console.log(data);
      } else {
        state.html = '';
        editorRef.value.innerHTML = '';
      }
    }
    // 清空文本，直接给指定 id 的文本赋空字符串
    async function clearContent() {
      state.html = '';
      editorRef.value.innerHTML = '';
      editContent();
    }
    // 编辑文本内容
    async function editContent(text = '') {
      text = editorRef.value.innerHTML;
      const { data } = await editContentReq({
        id: '1645414530651',
        text,
      });
      if (data.code == 200) {
        // 存储的时候顺便存储到本地，同步一下
        localStorage.setItem(
          'rich-text-storage',
          JSON.stringify(editorRef.value.innerHTML)
        );
        if (state.isSaveOffLine) window.alert('远程数据同步成功!');
        else window.alert('操作成功！');
        state.isSaveOffLine = false;
      }
    }
    // 同步数据
    function sameTheData() {
      if (state.isSaveOffLine)
        [editContent(JSON.parse(localStorage.getItem('rich-text-storage')))];
    }
    // 判断光标前面是否粗体
    function getSelDetail() {
      hidden();
      const selObj = window.getSelection();
      const range = selObj.getRangeAt(0);
      const startType = range.startContainer.parentElement.nodeName;
      const startColor = range.startContainer.parentElement.color;
      state.isBold = startType.toLowerCase() === 'b';
      if (range.startOffset < range.endOffset) {
        let color;
        switch (startColor) {
          case '#ff0000':
            color = 'red';
            break;
          case '#ffff00':
            color = 'yellow';
            break;
          case '#0000ff':
            color = 'blue';
            break;
          default:
            color = 'black';
        }
        state.fontColor = color;
        console.log(state.fontColor);
      } else {
        state.fontColor = 'black';
      }
    }
    // 改变粗体
    function changeBold() {
      state.isBold = !state.isBold;
    }
    // 网络设置
    function netSetting() {
      if (window.navigator.onLine === true) {
        console.log('首次连接');
        getContent();
        localStorage.setItem('rich-text-storage', JSON.stringify(state.html));
      } else {
        console.log('离线状态');
        state.isOffLine = true;
        if (localStorage.getItem('rich-text-storage')) {
          const html = JSON.parse(localStorage.getItem('rich-text-storage'));
          editorRef.value.innerHTML = html;
          state.html = html;
        } else {
          editorRef.value.innerHTML = '';
          state.html = '';
        }
      }
      EventUtil.addHandler(window, 'online', function () {
        console.log('online');
        sameTheData();
        state.isOffLine = false;
        window.alert('网络回来了！');
      });
      EventUtil.addHandler(window, 'offline', function () {
        state.isOffLine = true;
        window.alert('掉线了，可以使用离线存储到本地！');
      });
    }
    // 离线存储
    function saveOffContent() {
      state.isSaveOffLine = true;
      localStorage.setItem('rich-text-storage', JSON.stringify(state.html));
      window.alert('离线存储成功');
    }
    onMounted(() => {
      netSetting();
    });
    return {
      print,
      hidden,
      show,
      ...toRefs(state),
      clearContent,
      editContent,
      getSelDetail,
      changeBold,
      saveOffContent,
      editorRef,
    };
  },
};
</script>

<style lang="scss">
@import './CRichEditor.scss';
</style>
