import axios from 'axios';

const baseUrl = 'http://localhost:7001';

axios.defaults.baseURL = baseUrl;

// 获取所有文本内容
export function getAllContentReq() {
  return axios.get('/contents/findAll');
}

// 通过id获取文本内容
export function getContentByIdReq(id) {
  return axios.get(`/contents/findById/${id}`);
}

// 添加文本
export function addContentReq(data) {
  return axios.post('contents/add', data);
}

// 删除文本
export function clearContentReq(id) {
    return axios.delete(`contents/del/${id}`);
}

// 更改文本内容
export function editContentReq(data) {
    return axios.put('contents/edit', data);
}
